/*----------------------------------------*/
/* 00 - General 
/*----------------------------------------*/

@import url("https://fonts.googleapis.com/css2?family=Geologica&display=swap");

html {
  font-family: "Ubuntu", sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
}
body {
  margin: 0;
  overflow-x: hidden;
}
p {
  margin: 0 0 20px;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
abbr[title] {
  border-bottom: 1px dotted;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 0;
}
hr {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
code,
kbd,
pre,
samp {
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
select {
  text-transform: none;
}
button {
  overflow: visible;
}
button,
input,
select,
textarea {
  max-width: 100%;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
  opacity: 0.5;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input[type="checkbox"],
input[type="radio"] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin-right: 0.4375em;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #d1d1d1;
  margin: 0 0 1.75em;
  padding: 0.875em;
}
fieldset > :last-child {
  margin-bottom: 0;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
optgroup {
  font-weight: bold;
}
textarea:hover,
input:hover,
textarea:active,
input:active,
textarea:focus,
input:focus,
.form-control:focus {
  outline: 0px !important;
  -webkit-appearance: none;
  box-shadow: none !important;
}
.form-select {
  padding: 15px 15px;
  font-size: 15px;
  color: #848484;
  border: 1px solid #eeeeee;
  border-radius: 0;
  font-family: var(--pbmit-heading-typography-font-family);
  font-weight: 500;
}
.form-select:focus {
  border-color: #1bbde4;
  outline: 0;
  box-shadow: none;
}
.pbmit-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 130px;
}

/*----------------------------------------*/
/* 01 - Typography 
/*----------------------------------------*/
body {
  font-family: var(--pbmit-body-typography-font-family);
  font-weight: var(--pbmit-body-typography-variant);
  color: var(--pbmit-body-typography-color);
  font-size: var(--pbmit-body-typography-font-size);
  line-height: var(--pbmit-body-typography-line-height);
  color: var(--pbmit-body-typography-color);
  text-transform: none;
  font-style: normal;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--pbmit-heading-typography-font-family);
  font-weight: var(--pbmit-heading-font-variant);
  color: var(--pbmit-white-color);
}
h1 {
  font-size: 50px;
  line-height: 60px;
  letter-spacing: 0px;
}
h2 {
  font-size: 45px;
  line-height: 55px;
  letter-spacing: 0px;
}
h3 {
  font-size: 40px;
  line-height: 50px;
  letter-spacing: 0px;
}
h4 {
  font-size: 36px;
  line-height: 46px;
  letter-spacing: 0px;
}
h5 {
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 0px;
}
h6 {
  font-size: 24px;
  line-height: 34px;
  letter-spacing: 0px;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  -webkit-transition: all 0.5s ease-out 0s;
  -moz-transition: all 0.5s ease-out 0s;
  -ms-transition: all 0.5s ease-out 0s;
  -o-transition: all 0.5s ease-out 0s;
  transition: all 0.5s ease-out 0s;
  color: var(--pbmit-link-color-normal);
  text-decoration: none;
}
a:focus {
  text-decoration: none !important;
}
a:focus,
a:hover {
  color: var(--pbmit-link-color-hover);
  text-decoration: none !important;
}
a.link-btn,
.link-btn a {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 14px;
  letter-spacing: 1px;
}
a.link-btn i,
.link-btn a i {
  padding-left: 10px;
}
.pbmit-btn {
  display: inline-block;
  text-decoration: none;
  font-family: var(--pbmit-btn-typography-font-family);
  font-size: var(--pbmit-btn-typography-font-size);
  font-weight: var(--pbmit-btn-typography-variant);
  line-height: var(--pbmit-btn-typography-line-height);
  letter-spacing: 0.5px;
  padding: 18px 58px;
  text-transform: uppercase;
  background-color: var(--pbmit-global-color);
  border: none;
  border-radius: var(--pbmit-border-radius);
  box-shadow: var(--pbmit-box-shadow);
  color: #fff;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  cursor: pointer;
  outline: none !important;
  -webkit-font-smoothing: antialiased;
  text-align: center;
  transition: all 0.25s ease-in-out;
}
.pbmit-btn:hover,
.pbmit-btn:focus {
  color: #ffffff;
  background-color: var(--pbmit-blackish-color);
  box-shadow: var(--pbmit-box-shadow-hover);
  outline: none;
}
.pbmit-btn.pbmit-btn-outline {
  background-color: transparent;
  padding: 18px 58px;
  color: var(--pbmit-blackish-color);
  border: 2px solid var(--pbmit-blackish-color);
}
.pbmit-btn-outline:hover,
.pbmit-btn.pbmit-btn-outline:focus {
  background-color: var(--pbmit-blackish-color);
  color: #fff;
}
.pbmit-btn.pbmit-btn-outline-global {
  background-color: transparent;
  padding: 18px 58px;
  color: var(--pbmit-global-color);
  border: 2px solid var(--pbmit-global-color);
}
.pbmit-btn-outline-global:hover,
.pbmit-btn.pbmit-btn-outline-global:focus {
  background-color: var(--pbmit-global-color);
  color: #fff;
}
.pbmit-btn-hover-secondary {
  background-color: var(--pbmit-blackish-color);
}
.pbmit-btn-hover-secondary:hover,
.pbmit-btn.pbmit-btn-secondary {
  background-color: var(--pbmit-global-color);
}
.pbmit-btn-hover-white,
.pbmit-btn.pbmit-btn-white {
  background-color: #fff;
  color: var(--pbmit-blackish-color);
}
.pbmit-btn-hover-white:hover {
  background-color: var(--pbmit-global-color);
  color: #fff;
}
.pbmit-btn-hover-blackish {
  background-color: var(--pbmit-secondary-color);
}
.pbmit-btn-hover-secondary:hover,
.pbmit-btn.pbmit-btn-secondary {
  background-color: var(--pbmit-global-color);
}
.pbmit-btn-outline-white {
  color: #fff;
  background-color: transparent;
  border: 2px solid var(--pbmit-white-color);
}
.pbmit-btn-outline-white:hover {
  background-color: var(--pbmit-white-color);
  color: var(--pbmit-blackish-color);
}

.pbmit-btn-hover-global:hover {
  background-color: var(--pbmit-global-color);
  color: #fff;
}

.pbmit-btn.pbmit-btn-outline.pbmit-btn-hover-white:hover {
  border-color: #fff;
  background-color: #fff;
  color: var(--pbmit-global-color);
}
.pbmit-btn-inline {
  font-family: var(--pbmit-btn-typography-font-family);
  font-size: var(--pbmit-btn-typography-font-size);
  font-weight: var(--pbmit-btn-typography-variant);
  line-height: var(--pbmit-btn-typography-line-height);
  color: #ffffff;
  position: relative;
  border: none;
  padding: 0;
  margin-top: 0px !important;
  background-color: transparent !important;
  padding-bottom: 3px;
  letter-spacing: 0.5px;
}
.pbmit-btn-inline:hover,
.pbmit-btn-inline:focus {
  color: #ffffff;
}
.pbmit-btn-inline:hover:before,
.pbmit-btn-inline:focus:before {
  width: 0%;
}
.pbmit-btn span {
  position: relative;
  display: inline-block;
  padding-right: 4px;
  line-height: 14px;
  text-transform: uppercase;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}

/** Divider **/
.sep-line {
  height: 1px;
  border-top: 1px solid #ebebeb;
  display: block;
  position: relative;
  top: 1px;
  width: 100%;
}

/*----------------------------------------*/
/* 02 - Background Color 
/*----------------------------------------*/
body .pbmit-bg-color-global {
  background-color: var(--pbmit-global-color);
}
body .pbmit-bg-color-secondary {
  background-color: var(--pbmit-secondary-color);
}
body .pbmit-bg-color-light {
  background-color: var(--pbmit-light-color);
}
body .pbmit-bg-color-blackish {
  background-color: var(--pbmit-blackish-color);
}
body .pbmit-color-global {
  color: var(--pbmit-global-color);
}
body .pbmit-bg-color-white {
  background-color: var(--pbmit-white-color);
}

/*----------------------------------------*/
/* 03 - Background Position & Size 
/*----------------------------------------*/
.bg-cover {
  background-size: cover;
}
.bg-contain {
  background-size: contain;
}
.bg-pos-l {
  background-position: left;
}
.bg-pos-r {
  background-position: right;
}
.bg-pos-rt {
  background-position: right top;
}
.bg-pos-lt {
  background-position: left top;
}
.bg-pos-rb {
  background-position: right bottom;
}
.bg-pos-lb {
  background-position: left bottom;
}

/*----------------------------------------*/
/* 04 - Helper Class 
/*----------------------------------------*/
.section-mdxl {
  padding-top: 80px;
  padding-bottom: 130px;
}
.section-lg {
  background-color: var(--pbmit-secondary-color);
  padding-top: 100px;
  padding-bottom: 100px;
}
.section-lgx {
  padding-top: 100px;
  padding-bottom: 100px;
}
.section-lgt {
  padding-top: 100px;
  padding-bottom: 100px;
}
.section-lgb {
  padding-bottom: 100px;
}

/*----------------------------------------*/
/* 05 - Social icons 
/*----------------------------------------*/
.social-icons {
  list-style: none;
  margin: 0px;
  padding: 0px;
  font-size: 15px;
  padding-top: 2px;
  padding-bottom: 3px;
}
.social-icons li {
  display: inline-block;
  padding-left: 2px;
  padding-right: 2px;
  min-width: 34px;
}
.social-icons li > a {
  width: 30px;
  height: 30px;
  line-height: 30px;
  margin-right: 8px;
  background-color: transparent;
  font-size: 16px;
}

/*----------------------------------------*/
/* 06 - Header 
/*----------------------------------------*/
.navbar-brand .navbar-brand-item {
  height: 60px;
  display: block;
  width: auto;
}
.site-header {
  position: relative;
  z-index: 99;
  background-color: var(--pbmit-secondary-color);
}
.site-header .site-branding img {
  display: inline-block;
  max-width: 150px;
  height: 100px;
  max-height: 100px;
  z-index: 1;
  position: relative;
  -webkit-transition: all 0.4s ease-in-out;
  -moz-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.site-navigation ul.navigation > li > a {
  display: block;
  padding: 0px 15px;
  text-decoration: none;
  position: relative;
  z-index: 1;
  font-size: 13px;
  font-weight: 700;
  height: 120px;
  line-height: 120px;
  letter-spacing: 0px;
  text-transform: uppercase;
  color: var(--pbmit-white-color);
}
.site-navigation ul.navigation > li > a:hover {
  color: var(--pbmit-global-color);
}
.site-navigation ul.navigation > li > a:hover:after {
  color: var(--pbmit-global-color);
}
.site-navigation ul.navigation a {
  font-family: var(--pbmit-heading-typography-font-family);
  font-weight: 700;
  font-size: 15px;
}
.main-menu .navigation > li {
  position: relative;
  float: left;
  -webkit-transition: all 300ms ease;
  -moz-transition: all 300ms ease;
  -ms-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
}
.main-menu ul > li.dropdown > a:after {
  font-family: "pbminfotech-base-icons";
  content: "\e811";
  margin-left: 8px;
  font-size: 15px;
  font-weight: 600;
  vertical-align: middle;
}
.main-menu .navigation > li > ul,
.main-menu .navigation > li > ul > li > ul {
  margin-top: -20px;
  min-width: 200px;
  box-shadow: 0 0 60px 0 rgb(53 57 69 / 15%) !important;
  border-bottom: inherit;
  border-top: 4px solid var(--pbmit-global-color);
  position: absolute;
  left: 0;
  z-index: 100;
  visibility: hidden;
  background-color: var(--pbmit-blackish-color);
  opacity: 0;
  -webkit-transform: translateY(15px);
  -moz-transform: translateY(15px);
  -ms-transform: translateY(15px);
  -o-transform: translateY(15px);
  transform: translateY(15px);
  -webkit-box-shadow: 0px 3px 20px 0px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 3px 20px 0px rgba(0, 0, 0, 0.05);
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.05);
  -webkit-transition: all 0.3s ease-out;
  -moz-transition: all 0.3s ease-out;
  -ms-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.main-menu .navigation > li > ul > li > ul {
  left: 100%;
  top: 0;
}
.main-menu .navigation > li.dropdown:hover > ul,
.main-menu .navigation > li > ul > li.dropdown:hover > ul {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0px);
  -ms-transform: translateY(0px);
  transform: translateY(0px);
  z-index: 999;
}
.main-menu .navigation > li > ul li a {
  background-color: var(--pbmit-light-color);
}
.main-menu .navigation > li > ul a {
  padding: 10px 20px;
  display: block;
  font-weight: 700;
  font-size: 13px;
  line-height: 14px;
}

.main-menu .navigation > li > ul li.active a,
.main-menu .navigation > li > ul a:hover {
  color: var(--pbmit-global-color);
}
.main-menu .navigation > li > ul li.active a:hover {
  padding-left: 30px;
}
.main-menu .navigation > li > ul li a:hover {
  padding-left: 30px;
}
.header-button .pbmit-btn {
  padding: 15px 24px;
}
.header-button .pbmit-btn i {
  font-size: 20px;
}
.site-header .search-btn {
  margin-right: 15px;
  font-size: 17px;
}
.site-header .social-icons li > a {
  height: 50px;
  line-height: 50px;
}
.ipad-view-search {
  display: none;
}
.pbmit-link li {
  margin: 0 10px;
}
/** sticky-header **/
.sticky-header {
  position: fixed !important;
  top: 0px;
  width: 100%;
  background-color: var(--pbmit-light-color);
  z-index: 999;
  margin: 0;
  -webkit-box-shadow: 0px 13px 25px -12px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 0px 13px 25px -12px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 13px 25px -12px rgba(0, 0, 0, 0.25);
  padding: 0;
}
.site-header-menu .logo-img.stickylogo,
.site-header-menu.sticky-header .logo-img {
  max-height: 50px;
}
.site-header-menu.sticky-header .logo-img.stickylogo {
  display: inline-block;
}
.sticky-header .site-navigation ul.navigation > li > a {
  height: 100px;
  line-height: 100px;
}

.site-header-menu {
  transition: all 200ms ease;
  -moz-transition: all 200ms ease;
  -webkit-transition: all 200ms ease;
  -ms-transition: all 200ms ease;
  -o-transition: all 200ms ease;
}
.sticky-header {
  z-index: 999;
  opacity: 1;
  visibility: visible;
  -ms-animation-name: fadeInDown;
  -moz-animation-name: fadeInDown;
  -op-animation-name: fadeInDown;
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  -ms-animation-duration: 300ms;
  -moz-animation-duration: 300ms;
  -op-animation-duration: 300ms;
  -webkit-animation-duration: 300ms;
  animation-duration: 300ms;
  -ms-animation-timing-function: linear;
  -moz-animation-timing-function: linear;
  -op-animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -ms-animation-iteration-count: 1;
  -moz-animation-iteration-count: 1;
  -op-animation-iteration-count: 1;
  -webkit-animation-iteration-count: 1;
  animation-iteration-count: 1;
}
.fadeInDown {
  animation-name: fadeInDown;
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
  to {
    opacity: 1;
    transform: none;
  }
}
/* Pre Header */
.pre-header {
  height: 50px;
  line-height: 50px;
}
.list-unstyled i {
  font-size: 14px;
  color: #666;
}
.closepanel,
.pbmit-mobile-menu-bg,
.site-header .righticon {
  display: none;
}
/*----------------------------------------*/
/* 07 - Bootstrap Overwrite 
/*----------------------------------------*/
.g-lg-4,
.gy-lg-4,
.g-4,
.gy-4 {
  --bs-gutter-y: 2rem;
}
.g-lg-4,
.gx-lg-4,
.g-4,
.gx-4 {
  --bs-gutter-x: 2rem;
}
.row > * {
  padding-right: calc(var(--bs-gutter-x) * 0.6);
  padding-left: calc(var(--bs-gutter-x) * 0.6);
}

/*----------------------------------------*/
/* 08 - Swiper 
/*----------------------------------------*/
.swiper-slider {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.swiper-pagination-bullet {
  border: 0;
  background-color: #d3d5d7;
  outline: none;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  opacity: 1;
  vertical-align: middle;
}
.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 0;
}
.swiper-slider[data-dots="true"] {
  padding-bottom: 50px;
}
.swiper-pagination-bullet-active {
  border: 2px solid var(--pbmit-global-color);
  background: transparent;
  width: 20px;
  border-radius: 6px;
  background-color: var(--pbmit-global-color);
}
.swiper-button-next:after,
.swiper-button-prev:after {
  font-family: "pbminfotech-base-icons";
  font-size: 20px;
  color: var(--pbmit-blackish-color) !important;
  font-weight: 700;
  transition: all 500ms ease;
  -webkit-transition: all 500ms ease;
}
.swiper-button-next:after,
.swiper-rtl .swiper-button-prev:after {
  content: "\e814";
}
.swiper-button-prev:after,
.swiper-rtl .swiper-button-next:after {
  content: "\e814";
}
.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  transform: rotate(180deg);
  position: absolute;
  left: 14px;
}
.swiper-slider.home-case-studies {
  overflow: visible;
}
.home-case-studies .swiper-buttons {
  position: absolute;
  top: -100px;
  right: 0;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  left: 0;
  padding: 0 15px;
  text-align: right;
}
