     color: var(--pbmit-global-color);
} */
.swiper-slider.marquee .swiper-slide {
  display: flex;
  width: auto !important;
}
.swiper-slider.marquee .swiper-wrapper {
  -webkit-transition-timing-function: linear !important;
  -o-transition-timing-function: linear !important;
  transition-timing-function: linear !important;
}

/*----------------------------------------*/
/* 09 - Scroll To Top 
/*----------------------------------------*/
body .scroll-to-top {
  position: fixed;
  z-index: 200;
  opacity: 0;
  bottom: 10px;
  right: 20px;
  width: 45px;
  height: 45px;
  line-height: 40px;
  font-size: 16px;
  background-color: var(--pbmit-global-color);
  text-align: center;
  border: 3px solid #eeeeee;
  color: #fff;
  border-radius: 50%;
}
body .scroll-to-top.show {
  opacity: 1;
}
body .scroll-to-top:hover {
  color: #fff !important;
  background-color: var(--pbmit-secondary-color);
}

/*----------------------------------------*/
/*  10 - Search Box
/*----------------------------------------*/
.pbmit-search-overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  z-index: 100001;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--pbmit-secondary-color-rgb), 0.9);
  -webkit-transition: all 300ms;
  -o-transition: all 300ms;
  -moz-transition: all 300ms;
  transition: all 300ms;
  -webkit-transform: translateY(-30%);
  -moz-transform: translateY(-30%);
  -ms-transform: translateY(-30%);
  -o-transform: translateY(-30%);
  transform: translateY(-30%);
}
.pbmit-icon-close {
  position: absolute;
  color: #fff;
  right: 25px;
  top: 25px;
  cursor: pointer;
}
.pbmit-icon-close:before {
  content: "\e813";
  font-family: "pbminfotech-base-icons";
  font-size: 30px;
  font-weight: normal;
  opacity: 0.5;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
  display: block;
  opacity: 1;
}
.pbmit-search-outer {
  max-width: 970px;
  margin: 0 auto;
  position: relative;
  top: 50%;
  left: 0;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  transform: translateY(-50%);
}
.pbmit-search-outer .pbmit-search-logo {
  margin-bottom: 60px;
  position: absolute;
  margin-top: -125px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  -khtml-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -ms-transform: translateX(-50%) translateY(-50%);
  -o-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}
.pbmit-search-outer .pbmit-search-logo img {
  max-height: 65px;
}
.pbmit-search-overlay .pbmit-site-searchform {
  position: relative;
}
.pbmit-search-overlay input[type="search"] {
  border-bottom-color: #7fc540;
}
.pbmit-search-overlay input[type="search"] {
  border: none;
  font-size: 16px;
  color: #09162a;
  padding: 0;
  padding-left: 16px;
  text-align: left;
  height: 70px;
  line-height: 70px;
}
.pbmit-search-overlay .pbmit-site-searchform button {
  position: absolute;
  top: 5px;
  right: 5px;
  padding: 10px;
  font-size: 20px;
  height: 60px;
  line-height: 60px;
  padding: 0;
  width: 60px;
  border: 0;
  border-radius: 4px;
  color: #fff;
  background: var(--pbmit-global-color);
}
.pbmit-search-overlay.st-show {
  display: block;
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateY(0);
  -moz-transform: translateY(0);
  -ms-transform: translateY(0);
  -o-transform: translateY(0);
  transform: translateY(0);
}

.pbmit-base-icon-menu-1 {
  color: var(--pbmit-white-color);
}
